export type EntityOperationEvent<T> = EntitySetEvent<T> | EntityAdditionEvent<T> | EntityRemovalEvent<T>;

export interface EntitySetEvent<T> {
  set: T[];
}

export interface EntityAdditionEvent<T> {
  add: T[];
}

export interface EntityRemovalEvent<T> {
  remove: T[];
}

export function isSetEvent(event: EntityOperationEvent<unknown>): event is EntitySetEvent<unknown> {
  return Object.prototype.hasOwnProperty.call(event, 'set');
}
export function isAdditionEvent(event: EntityOperationEvent<unknown>): event is EntityAdditionEvent<unknown> {
  return Object.prototype.hasOwnProperty.call(event, 'add');
}
export function isRemovalEvent(event: EntityOperationEvent<unknown>): event is EntityRemovalEvent<unknown> {
  return Object.prototype.hasOwnProperty.call(event, 'remove');
}
